import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import s from './Clock.module.css'
import moment, {Moment} from "moment";
import 'moment/locale/be';

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const unitDate = moment();
    const [date, setDate] = useState<Moment>(unitDate)
    const [show, setShow] = useState<boolean>(false)


    const start = () => {
        stop();
        let newTimerId: number = window.setInterval(() => {  //   +setInterval     string  number
            setDate(moment())
        }, 1000)
        setTimerId(newTimerId);

        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    }

    const stop = () => {
        clearInterval(timerId);
        setTimerId(undefined);
        //   //    ,    (timerId <- undefined)

    }

    const onMouseEnter = () => { //   //     
        setShow(!show);
    }
    const onMouseLeave = () => { //   //      
        setShow(!show);
    }



    const stringTime = date.format('h:mm:ss a'); // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = date.format('MM.DD.YYYY'); // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = moment().format('dddd'); //  
    const stringMonth = moment().format('MMMM'); //  

    return (
        <div className={s.clock}>
            <div
                id={'hw91-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw91-day'}>{stringDay}</span>,{' '}
                <span id={'hw91-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw91-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw91-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw91-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw91-button-start'}
                    disabled={!!timerId} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw91-button-stop'}
                    disabled={!timerId} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
